import React from 'react';
import logo from './logo1.png';
import './App.css';


function Header(props) {
  return (
    <div className="App">
      <body className="bodyStructure">
          <div class="container">
            <div class="row">
              <div class="col-sm">
                <header className="App-header">
                  <div class= "welcome">
                    {props.loggedIn ? `welcome, ${props.name}` : 'Please log in'}
                  </div>
                  <img src={logo} className="App-logo" alt="logo" />
                  <h1>
                      PJ Production <br/>
                  </h1>
                  <div>
                      the media Industry<br/>
                  </div><br/>
                </header>
                <div class="navigationBar ">
                  <nav>
                      <ul>
                          <li class="nav1"><a href="#">Home</a></li> 
                          <li class="nav1"><a href="#">review</a></li>
                          <li class="nav1"><a href="#">project</a></li>
                          <li class="nav1"><a class="active" href="#">About</a></li>
                          <li class="nav1"><a href="#">Contact</a></li> 
                      </ul>
                  </nav>
                </div>
              </div>
            </div>
                   
          </div>
          
      </body>
    </div>);
  }

export default Header;
